import { NgClass, TitleCasePipe } from '@angular/common';
import { Component, EventEmitter, HostListener, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { FrontendLogger } from '@biz-nest/core-client';
import { OwlDateTimeModule, OwlNativeDateTimeModule, SelectMode } from '@danielmoncada/angular-datetime-picker';
import { EventbusService } from '@front-shared-lib';
import { runSubscriptionsForSharedUse } from '@main-shared-lib';
import { openClose } from '../../../animations/general.animation';

@Component({
  selector: 'lib-solo-date',
  standalone: true,
  imports: [
    FormsModule,
    ReactiveFormsModule,
    OwlDateTimeModule,
    OwlNativeDateTimeModule,
    TitleCasePipe,
    NgClass
  ],
  animations: [
    openClose
  ],
  templateUrl: './solo-date.component.html',
  styleUrl: './solo-date.component.scss'
})
export class SoloDateComponent
implements OnInit, OnDestroy {
  @Output() notify: EventEmitter<Date | undefined> = new EventEmitter<Date | undefined>();
  @Output() notifyRange: EventEmitter<[Date, Date] | undefined> = new EventEmitter<[Date, Date] | undefined>();
  @Input() disabled = false;
  @Input() name?: string;
  @Input() soleDate?: string;
  @Input() placeholder = 'YYYY-MM-DD';
  @Input() selectMode: SelectMode = 'single';
  @Input() cssClass?: string;

  popActive = false;
  rangeDate: Date[] | null = null;
  cleanup?: () => void;
  pickerOpen = true;

  constructor(private eventbus: EventbusService) { }

  ngOnInit(): void {
    const handlers = {
      clearRangeDate: () => {
        this.rangeDate = null;
        this.soleDate = undefined;
        this.notifyRange.emit(undefined);
        this.pickerOpen = false;
        setTimeout(() => {
          this.pickerOpen = true;
        }, 100);
      }
    };

    const { cleanup } = runSubscriptionsForSharedUse(this.eventbus, handlers);

    this.cleanup = cleanup;
  }

  @HostListener('document:click', ['$event'])
  onDocumentClick() {
    if (this.popActive) {
      this.popActive = false;
    }
  }

  onRangeChange(event: string) {
    //
    const date = new Date(event);

    if (this.rangeDate && this.rangeDate[0]) {
      // compare with first date
      if (date < this.rangeDate[0]) {
        this.rangeDate = null;
      } else {
        // eslint-disable-next-line no-lonely-if
        if (this.rangeDate[1]) {
          this.rangeDate = null;
        } else {
          this.rangeDate.push(date);
        }
      }
    } else {
      this.rangeDate = [date];
    }

    if (!this.rangeDate) {
      return;
    }

    let fromDate = '';
    let toDate = '';

    if (this.rangeDate[0]) {
      fromDate = this.rangeDate[0]
        .getFullYear() + '-' + (this.rangeDate[0].getMonth() + 1) + '-' + this.rangeDate[0].getDate();
    }
    if (this.rangeDate[1]) {
      toDate = this.rangeDate[1]
        .getFullYear() + '-' + (this.rangeDate[1].getMonth() + 1) + '-' + this.rangeDate[1].getDate();
    }

    this.soleDate = fromDate + ' - ' + toDate;

    FrontendLogger.debug('range date is ', this.rangeDate);
    if (this.rangeDate && this.rangeDate[0] && this.rangeDate[1] && this.rangeDate[0] !== this.rangeDate[1]) {
      this.popActive = false;
      this.notifyRange.emit([this.rangeDate[0], this.rangeDate[1]]);
      this.rangeDate = null;
    }
  }

  onDateChange(event: string) {
    if (this.selectMode === 'range') {
      this.onRangeChange(event);
    } else {
      this.onSoloDateChange(event);
    }
  }

  onSoloDateChange(event: string) {
    FrontendLogger.debug('date selected ', event);
    this.popActive = false;
    const date = new Date(event);
    const newDate = date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate();

    if (newDate === this.soleDate) {
      return;
    }
    if (date.toString() === 'Invalid Date') {
      FrontendLogger.error('date is INVALID', newDate);

      this.soleDate = '';
      this.notify.emit(undefined);

      return;
    }
    this.soleDate = newDate;
    this.notify.emit(date);
  }

  inputClicked() {
    this.popActive = !this.popActive;
  }

  ngOnDestroy() {
    this.cleanup?.();
  }
}

